<template>
  <section  class="center-bestGoals">
    <div class="container mx-auto p-1">
      <div class="w-full h-auto">
        <div class="flex justify-end">
          <span class="text-end font-semibold text-slate-700 ml-4">پاس</span>
          <span class="text-end font-semibold text-slate-700">گل</span>
        </div>
        <div  class="flex flex-col space-y-2 w-full">
          <div v-for="items in menu" :key="items.id" class="w-full">
            <div v-for="(value,index) in items" :key="value">
              <div class="flex flex-row justify-between w-full my-4">
                <div class=" w-9/12 flex flex-row">
                  <p>{{index+=1}}</p>
                  <img @error="notShow" :src="`${value.player.image}`" alt="" class="w-12 h-12 rounded-full mx-1"/>
                  <div class="flex flex-col">
                    <p class="text-black text-sm font-semibold text-right my-2">{{value.player.name_fa}}</p>
                    <div class="flex flex-row">
                      <img @error="notShow" :src="`${value.team.logo}`" alt="" class="w-6 h-6 rounded-full mx-1">
                      <p class="text-sm text-semibold text-slate-900 text-right mx-1">{{value.team.name_fa}}</p>
                    </div>
                  </div>
                </div>
                <div class="flex flex-row justify-around w-3/12">
                  <p class=" result text-sm text-slate-900 font-thin">{{value.assist}}</p>

                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import{reactive,onMounted,ref} from "vue";
import EndPoints from "@/service/EndPoints";
import {handleError} from "@/components/composable/handleError";
// import VLazyImage from "v-lazy-image";
export default {
  name: "AllAssits",
  components: {},
  setup(){
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_player_default.png')
    const menu=reactive([])
    const result=onMounted(()=>{
      EndPoints.getGoalsGermany()
          .then((response)=>{
            if(response.data.success){
              menu.push(response.data.data.assist)
            }
          })
          .catch((error)=>{
            handleError(error)
          })
    })
    function notShow(event){
      event.target.src=notFound.value
    }
    return{
      menu,
      result,
      notFound,
      notShow
    }
  }
}
</script>

